<template>
    <div id="viewEmp">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{emp_name}}</h4></li>
            <li class="collection-item">emp_id : {{emp_id}}</li>
            <li class="collection-item">position : {{position}}</li>
        </ul>
        <router-link to="/" class="btn grey" >Back</router-link>
        <button v-on:click="deleteEmp" class="btn red">Delete</button>
    
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name : 'edit-emp',params : {emp_id : emp_id} }" class="btn-floating btn-large red">
            <i class="fa fa-pencil"></i>
            </router-link>
        </div>

    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'viewEmp',
    data () {
        return {
            doc_id : null,    
            emp_id : null,
            emp_name : null,
            position : null
        }
    },
    beforeRouteEnter  (to, from, next) {
        // 이 컴포넌트를 렌더링하는 라우트가 이전으로 네비게이션 될 때 호출됩니다.
        // `this` 컴포넌트 인스턴스에 접근 할 수 있습니다.
        // beforeRouteEnter 가드는 네비게이션이 확인되기 전에 가드가 호출되어서 새로운 엔트리 컴포넌트가 아직 생성되지 않았기 때문에 this에 접근하지 못합니다.
        // 그러나 콜백을 next에 전달하여 인스턴스에 액세스 할 수 있습니다. 네비게이션이 확인되고 컴포넌트 인스턴스가 콜백에 전달인자로 전달 될 때 콜백이 호출됩니다.
        db.collection('emp').where('emp_id', '==', to.params.emp_id) .get().then(
            (querySnapShot) => {
                querySnapShot.forEach(doc => {
                    next(vm => {
                        // `vm`을 통한 컴포넌트 인스턴스 접근
                        vm.doc_id = doc.id
                        vm.emp_id = doc.data().emp_id
                        vm.emp_name = doc.data().emp_name
                        vm.position = doc.data().position
                    })
                })
            })
  },
  /*
  watch : {
      '$route' : 'fetchData'
  },
  */
  methods : {
      /*
      fetchData() {
          db.collection('emp').where('emp_id', '==', this.$route.params.emp_id).get()
          .then(querySnapShot => {
                  querySnapShot.forEach(doc => {
                      this.doc_id = doc.id
                      this.emp_id = doc.data().emp_id
                      this.emp_name = doc.data().emp_name
                      this.position = doc.data().position
                  })
              })
      },
      */
      deleteEmp() {
          if(confirm('Are you sure?')) {
              db.collection('emp').doc(this.doc_id).delete()
              this.$router.push('/')              
          }
      }
  }

}
</script>



