<template>
    <div id="editEmp">
        <h3>editEmp</h3>
        <div class="row">
            <form v-on:submit.prevent="saveEmp" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="emp_id" disabled>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="emp_name" required>
                    </div>
                </div>
                
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>

            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'editEmp',
    data () {
        return {
            doc_id : null,    
            emp_id : null,
            emp_name : null,
            position : null
        }
    },
    beforeRouteEnter  (to, from, next) {
        // 이 컴포넌트를 렌더링하는 라우트가 이전으로 네비게이션 될 때 호출됩니다.
        // `this` 컴포넌트 인스턴스에 접근 할 수 있습니다.
        // beforeRouteEnter 가드는 네비게이션이 확인되기 전에 가드가 호출되어서 새로운 엔트리 컴포넌트가 아직 생성되지 않았기 때문에 this에 접근하지 못합니다.
        // 그러나 콜백을 next에 전달하여 인스턴스에 액세스 할 수 있습니다. 네비게이션이 확인되고 컴포넌트 인스턴스가 콜백에 전달인자로 전달 될 때 콜백이 호출됩니다.
        db.collection('emp').where('emp_id', '==', to.params.emp_id) .get().then(
            (querySnapShot) => {
                querySnapShot.forEach(doc => {
                    next(vm => {
                        // `vm`을 통한 컴포넌트 인스턴스 접근
                        vm.doc_id = doc.id
                        vm.emp_id = doc.data().emp_id
                        vm.emp_name = doc.data().emp_name
                        vm.position = doc.data().position
                    })
                })
            })
  },
  watch : {
      '$route' : 'fetchData'
  },
  methods : {
        fetchData() {
            db.collection('emp').where('emp_id', '==', this.$route.params.emp_id).get()
            .then(querySnapShot => {
                    querySnapShot.forEach(doc => {
                        this.doc_id = doc.id
                        this.emp_id = doc.data().emp_id
                        this.emp_name = doc.data().emp_name
                        this.position = doc.data().position
                    })
                })
        },
        saveEmp() {
            db.collection('emp').doc(this.doc_id).update({
                emp_id : this.emp_id,
                emp_name : this.emp_name,
                position : this.position
            }).then(() => {
                this.$router.push({name : 'view-emp', params : {emp_id : this.emp_id}})
            }).catch(error => {
                console.log(error)
            })
        }
    }

}
</script>



