import Vue from 'vue'
import Router from 'vue-router'
import dashboard from '@/components/dashboard'
import newEmp from '@/components/newEmp'
import editEmp from '@/components/editEmp'
import viewEmp from '@/components/viewEmp'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: dashboard
    },
    {
      path: '/vueFirebaseEmpManager/new',
      name: 'new-emp',
      component: newEmp
    },
    {
      path: '/vueFirebaseEmpManager/edit/:emp_id',
      name: 'edit-emp',
      component: editEmp
    },
    {
      path: '/vueFirebaseEmpManager/:emp_id',
      name: 'view-emp',
      component: viewEmp
    }
  ]
})



