<template>
    <div id="dashboard">
        <ul class="collection with-headerr">
            <li class="collection-header"><h4>emp list</h4></li>
            <li v-for="emp in empList" v-bind:key="emp.emp_id" class="collection-item">
                {{emp.emp_id}} : {{emp.emp_name}} 
                <router-link class="secondary-content" v-bind:to="{name : 'view-emp', params: {emp_id:emp.emp_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>

            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link v-bind:to="{name : 'new-emp'}" class="btn-floating btn-large red">
            <i class="fa fa-plus"></i>
            </router-link>
        </div>


    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            empList:[]
        }
    },
    created () {
      
      /* 단순조회 */ 
      db.collection('emp').orderBy('emp_id').get().then((querySnapshot) => {        
        querySnapshot.forEach((doc) => {
        
          const data = {
            'id': doc.id,
            'emp_id': doc.data().emp_id,
            'emp_name': doc.data().emp_name,
            'position': doc.data().position
          }
          this.empList.push(data)
        })
      })

      /* DB에 변경 이벤트가 있으면 자동으로 데이터를 가져온다  
      db.collection('emp').onSnapshot(snapshot => {
          snapshot.docChanges().forEach(change => {
              if(change.type == 'added'){
                  let doc = change.doc  
                  const data = {
                    'id': doc.id,
                    'emp_id': doc.data().emp_id,
                    'emp_name': doc.data().emp_name,
                    'position': doc.data().position
                }
                this.empList.push(data)
              }
              
          });
      })
      */
      
    }

}
</script>



