<template>
    <div id="newEmp">
        <h3>new-emp</h3>
        
        <div class="row">
            <form v-on:submit.prevent="saveEmp" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="emp_id" required>
                        <label>emp_id : </label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="emp_name" required>
                        <label>emp_name : </label>
                    </div>
                </div>
                
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                        <label>position : </label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>

            </form>
        </div>
    </div>
</template>

<script>

import db from './firebaseInit'
export default {
    name: 'newEmp',
    data () {
        return {
            emp_id : null,
            emp_name : null,
            position : null
        }
    },
    methods : {
        saveEmp () {
            db.collection('emp').add({
                emp_id : this.emp_id,
                emp_name : this.emp_name,
                position : this.position
            }).then(docRef => {
                this.$router.push('/')
            }).catch(error => {
                console.log(error)
            })
        }
    }


}
</script>



